@import url('https://fonts.googleapis.com/css2?family=Acme&family=Poppins:ital,wght@0,300;0,400;0,500;0,600;1,200&family=Satisfy&display=swap');
body{
  line-height: 1.5;
  font-family: 'Poppins' sans-serif;
}
*{
  margin: 0;
  padding: 0;
  outline:none;
  text-decoration: none;
  box-sizing: border-box;
}
::after,::before{
  box-sizing: border-box;
}
ul{
  list-style: none;
}
.hidden{
  display: none !important;
}
.row{
  display: flex;
}
.mg-top{
  margin-top:25px;
}
.shadow-dark{
  box-shadow: 0 0 20px rgba(48, 46, 77, 0.15);
}