@import url("https://fonts.googleapis.com/css2?family=Acme&family=Poppins:ital,wght@0,300;0,400;0,500;0,600;1,200;700&family=Satisfy&display=swap");
.ksb {
    background: white;
    width: 270px;
    position: fixed;
    left: 0;
    top: 0;
    border-right: 1px solid #302e4d;
    padding: 30px;
    height: 100%;
    z-index: 10;
    display: flex;
    justify-content: center;
    align-items: center;
}
.ksb .ksb-logo {
    position: absolute;
    top: 50px;
    font-size: 30px;
    text-transform: capitalize;
}
.ksb .ksb-logo a {
    color: black;
    font-weight: 700;
    padding: 15px 20px;
    font-style: 30px;
    letter-spacing: 5px;
    position: relative;
}
.ksb .ksb-logo a span {
    font-family: "Clicker Script", cursive;
    font-family: 40px;
}
.ksb .ksb-logo a::before{
    content: "";
    position: absolute;
    width: 25px;
    height: 25px;
    border-bottom: 5px solid #ec1839;
    border-left: 5px solid #ec1839;
    left: 0;
    bottom: 0;
}
.ksb .ksb-logo a::after{
    content: "";
    position: absolute;
    width: 25px;
    height: 25px;
    border-top: 5px solid #ec1839;
    border-right: 5px solid #ec1839;
    right: 0;
    top:  0;
}
.ksb .nav-toggler{
    height: 40px;
    width: 45px;
    border: 1px solid #302e4d;
    cursor: pointer;
    position: fixed;
    left: 300px;
    top: 20px;
    border-radius: 5px;
    background: blanchedalmond;
    display: none;
    justify-content: center;
    align-items: center;
}
.ksb .nav-toggler span{
    height: 2px;
    position: relative;
    width: 18px;
    background: #ec1839;
    display: inline-block;
}
.ksb .nav-toggler span::before{
    content: "";
    height: 2px;
    width: 18px;
    background: #ec1839;
    position: absolute;
    top: -6px;
    left: 0;
}
.ksb .nav-toggler span::after{
    content: "";
    height: 2px;
    width: 18px;
    background: #ec1839;
    position: absolute;
    top: 6px;
    left: 0;
}
.ksb .nav{
    margin-top: 50px;
}
.ksb .nav li{
    margin-bottom: 20px;
    display: block;
}
.ksb .nav li a{
    font-size: 20px;
    font-weight: 600;
    display: block;
    border-bottom: 1px solid #302e4d;
    color: black;
    padding: 5px 15px;
}
.ksb .nav li a.active{
color: #ec1839;
}
.ksb .nav li a i{
    margin-right: 15px;
}
.header {
    color: wheat;
    opacity: 1;
    z-index: 0;
    background: #f2f2fc;
    min-height: 100vh;
    padding: 0 30px;
    display: block;
    /* position: fixed; */
    left: 270px;
    top: 0;
    right: 0;
}
.main-content{
    padding-left: 270px;
   background: #f2f2fc;
}
.home-img img{
    height:400px;
    margin: auto;
    border-radius: 5px;
}
.padd-15{
    padding-left: 15px;
    padding-right: 15px;
}
.container{
    max-width: 1100px;
    width: 100%;
    margin: auto;
}
.section .container{
    padding-top: 60px;
    padding-bottom: 70px;
}
.section-title{
    flex: 0 0 100%;
    max-width: 100%;
    margin-bottom: 60px;
}
.section-title h2{
    font-size: 40px;
    color: black;
    font-weight: 700;
    position: relative;
}.section-title h2::before{
    content: '';
    height: 4px;
    width: 50px;
    background: #ec1839;
    position: absolute;
    top: 100%;
    left: 0;
}
.section-title h2::after{
    content: '';
    height: 4px;
    width: 25px;
    background: #ec1839;
    position: absolute;
    top: 100%;
    left: 0;
    margin-top: 8px;
}
.row{
    display: flex;
    flex-wrap: wrap;
    margin-left: -15px;
    margin-right: -15px;
    position: relative;
}
.btn{
    font-size: 16px;
    font-weight: 500;
    padding: 12px 35px;
    color: white;
    border-radius: 40px;
    background: #ec1839;
    display: inline-block;
    border: none;
    cursor: pointer;
    white-space: nowrap;
    transition: all .3s ease;
}
.btn:hover{
    transform: scale(1.05);
}
.section{
    min-height: 100vh;
    display: flex;
    color: black;
}
.section .home-info{
    flex: 0 0 60%;
    max-width: 60%;
    color: black;
}
h3.hello{
    font-size: 28px;
    margin: 15px 0;
    color: black;
}
h3.hello span{
    font-family: 'Clicker Script',cursive;
    font-size: 30px;
    font-weight: 700;
    color: #ec1839;
}
h3.my-profession{
    font-size: 30px;
    margin: 15px 0;
    color: black;
}
.typ{
    color: #ec1839;
}
.home-info p{
    margin-bottom: 70px;
    font-size: 20px;
}
.section .home-img{
    flex: 0 0 40%;
    max-width: 40%;
    text-align: center;
    position: relative;
}.home-img::after{
    content: "";
    position: absolute;
    height: 85px;
    width: 85px;
    right: -10px;
    bottom: -40px;
    border-bottom: 10px solid #ec1839;
    border-right: 10px solid #ec1839;
}
.home-img::before{
    content: "";
    position: absolute;
    height: 85px;
    width: 85px;
    left: -20px;
    top: -40px;
    border-top: 10px solid #ec1839;
    border-left: 10px solid #ec1839;
}
.about .about-content{
    flex: 0 0 100%;
    max-width: 100%;

}
.about .about-content .about-text{
    flex: 0 0 100%;
    max-width: 100%;
}
.about .about-content .about-text h3{
    font-size: 24px;
    margin-bottom: 15px;
    color: #302e4d;
    font-weight: 700;
}
.about .about-content .about-text h3 span{
    color: #ec1839;
}
.about .about-content .about-text p{
    font-size: 16px;
    line-height: 25px;
    color: #302e4d;
}
.about .about-content  .personal-info{
    flex: 0 0 60%;
    max-width: 60%;
    margin-top: 40px;
}
.about .about-content .skills{
    flex: 0 0 40%;
    max-width: 40%;
margin-top: 40px;
}
.about .about-content  .personal-info .info-item{
    flex: 0 0 50%;
    max-width: 50%;
}
.about .about-content  .personal-info .info-item p{
font-weight: 600;
padding: 10px 0;
font-size: 16px;
border-bottom: 1px solid #504e70;
}
.about .about-content .personal-info .info-item p span{
    color: #504e70;
    font-weight: 400;
    margin-left: 4px;
    display: inline-block;
}
.about .about-content  .personal-info .buttons{
    margin-top: 30px;
}
.about .about-content .skills .skill-item{
    flex: 0 0 100%;
    max-width: 100%;
}
.about .about-content .skills .skill-item h5{
    line-height: 40px;
    font-weight: 600;
    font-size: 16px;
    color: #302e4d;
    text-transform: capitalize;
}
.about .about-content .skills .skill-item .progress{
    background: #e8dfec;
    height: 7px;
    border-radius: 4px;
    width: 100%;
    position: relative;
}
.about .about-content .skills .skill-item{
    margin-bottom: 25px;
}
.about .about-content .skills .skill-item .progress .progress-in{
position: absolute;
left: 0;
top: 0;
height: 100%;
border-radius: 4px;
background-color: #ec1839;
}
.about .about-content .skills .skill-item .skill-percent{
    position: absolute;
    right: 0;
    color: black;
    top: -40px;
    font-weight: 400;
    line-height: 40px;

}
.about .about-content .education{
    flex: 0 0 50%;
    max-width: 50%;
    margin-top: 30px;

}
.about .about-content h3.title{
    font-size: 24px;
    margin-bottom: 30px;
    font-weight: 700;
    color: black;
}
.about .about-content .timeline-box{
    flex: 0 0 100%;
    max-width: 100%;

}
.about .about-content .timeline{
    background: #fdf9ff;
    padding: 30px 15px;
    border: 1px solid #e8dfec;
    border-radius: 10px;
    width: 100%;
    position: relative;
}
.about .about-content .timeline .timeline-item{
    position: relative;
    padding-left: 37px;
    padding-bottom: 50px;
}
.about .about-content .timeline .timeline-item:last-child{
    padding-bottom: 0;
}
.about .about-content .timeline .timeline-item::before{
    content: "";
    width: 1px;
    position: absolute;
    height: 100%;
    left: 7px;
    top: 0;
    background: #ec1839;
}
.about .about-content .timeline .circle-dot{
    position: absolute;
    left: 0;top: 0;
    height: 15px;width: 15px;
    border-radius: 50%;
    background: #ec1839;
}
.about .about-content .timeline .timeline-date{
    font-weight: 400;
    font-size: 14px;
    margin-bottom: 12px;
    color: #504e70;
}
.about .about-content .timeline .timeline-date .fa{
    margin-right: 5px;
}
.about .about-content .timeline .timeline-title{
    font-weight: 700;
    font-size: 18px;
    margin-bottom: 15px;
    text-transform: capitalize;
    color: #302e4d;
}
.about .about-content .timeline .timeline-text{
    line-height: 25px;
    font-size: 16px;
    text-align: justify;
    color: #504e70;
}
.portfolio .portfolio-heading{
    flex: 0 0 100%;
    max-width: 100%;
    margin-bottom: 40px;
}
.portfolio .portfolio-heading h2{
    color: #302e4d;
    font-weight: 500;
}
.portfolio .container {
    padding-bottom: 40px;
}
.portfolio .portfolio-item{
    flex: 0 0 33.33%;
    max-width: 33.33%;
margin-bottom: 30px;
}
.portfolio .portfolio-item-inner{
    border: 6px solid #fdf9ff;
    border-radius: 10px;
    overflow: hidden;
    cursor: pointer;
}
.portfolio .portfolio-item-inner .portfolio-img img{
    width: 100%;
    display: block;
}
.contact-title{
color: #ec1839;
text-align: center;
font-size: 25px;
margin-bottom: 20px;
}
.contact-sub-title{
color: #302e4d;
text-align: center;
font-size: 15px;
margin-bottom: 60px;
}
.contact .contact-info-item{
    flex: 0 0 25%;
    max-width: 25%;
    text-align: center;
    margin-bottom: 60px;
}
.contact .contact-info-item .icon{
    display: inline-block;
}
.contact .contact-info-item .icon .fa{
    font-size: 25px;
    color: #ec1839;
}
.contact .contact-info-item h4{
    font-size: 18px;
    font-weight: 700;
    color: #302e4d;
    text-transform: capitalize;
    margin: 15px 0 5px;
}
.contact .contact-info-item p{
    font-size: 16px;
    line-height: 25px;
    color: #504e70;
    font-weight: 400;
}.contact .contact-form{
    flex: 0 0 100%;
    max-width: 100%;
}
.contact .contact-form .col-6{
    flex: 0 0 50%;
    max-width: 50%;
}
.contact .contact-form .col-12{
    flex: 0 0 100%;
    max-width: 100%;
}
.contact .contact-form .form-item{
margin-bottom: 30px;
}
.contact .contact-form .form-item .form-control{
    width: 100%;
    height: 50px;
    border-radius: 25px;
    background: #fdf9ff;
    border: 1px solid #e8dfec;
    padding: 10px 25px;
    font-size: 16px;
    color: #504e70;
    transition: all 0.3s ease;
}
.contact .contact-form .form-item .form-control:focus{
    box-shadow: 0 0 20px rgba(48, 46, 77, 0.15);
}
.contact .contact-form .form-item textarea.form-control{
    height: 140px;
}
.contact .contact-form .btn{
    height: 50px;
    padding: 0 50px;
}
@media (max-width:1199px){
    .ksb{
        left: -270px;
    }
    .main-content{
        padding-left: 0;
    }
    .about .about-content .personal-info .info-item p span{
        display: block;
        margin-left: 0;
    }
}
@media (max-width:991px){
.header .home-img{
    display: none;
}

.header .home-info{
    flex: 0 0 100%;
    max-width: 100%;
}
.contact .contact-info-item,
.portfolio .portfolio-item{
    flex: 0 0 50%;
    max-width: 50%;
}
}
@media (max-width:767px){
    .contact .contact-form .col-6,
.contact .contact-info-item,
.portfolio .portfolio-item,
.about .about-content .education,
.about .about-content .skills,
.about .about-content .personal-info{
    flex: 0 0 100%;
    max-width: 100%;
}
}